img {
  width: 500px;
  margin-right: 30px;
}

span {
  font-weight: 600;
}

.cat-info {
  margin-top: 20px;
  display: flex;
}

.cat-description {
  width: 50%;
}

.loading {
  color: #000;
  font-size: 20px;
  font-weight: 600;
}

.loader {
  width: 15px;
  height: 15px;
  box-sizing: border-box;
  border: 3px solid #000;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin-right: 5px;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.breed-select {
  width: 200px;
  height: 35px;
  background-color: #9dbeb4;
  border-radius: 3px;
  font-size: 15px;
}

